import uniq from '@tinkoff/utils/array/uniq';

class Hooks {
    constructor() {
        this.hooks = Object.create(null);
    }
    /**
     * Регистрация хуков
     */
    registerHooks(name, list) {
        if (!this.hooks[name]) {
            this.hooks[name] = [];
        }
        this.hooks[name] = uniq(this.hooks[name].concat(list));
    }
    /**
     * Запуск синхронных хуков, payload проходит через все хуки и будет результатом выполнения
     */
    // eslint-disable-next-line max-params
    runHooks(name, context, payload, options) {
        const plugins = this.hooks[name];
        if (!plugins) {
            return payload;
        }
        let current = payload;
        for (let i = 0; i < plugins.length; i++) {
            current = plugins[i](context, current, options);
        }
        return current;
    }
    /**
     * Запуск ассихронных хуков
     */
    runAsyncHooks(name, context, payload, options) {
        const plugins = this.hooks[name];
        if (!plugins) {
            return payload;
        }
        for (let i = 0; i < plugins.length; i++) {
            const plugin = plugins[i];
            setTimeout(() => {
                plugin(context, payload, options);
            });
        }
        return payload;
    }
    /**
     * Запуск проммис хуков
     */
    runPromiseHooks(name, context, options) {
        const plugins = this.hooks[name];
        return (payload) => {
            if (!plugins) {
                return Promise.resolve(payload);
            }
            let currentId = 0;
            return Promise.resolve(payload).then(function resolver(data) {
                // eslint-disable-next-line no-plusplus
                const plugin = plugins[currentId++];
                if (!plugin) {
                    return data;
                }
                // eslint-disable-next-line promise/no-nesting
                return Promise.resolve(plugin(context, data, options)).then(resolver);
            });
        };
    }
}

export { Hooks };
