import { createToken } from '@tinkoff/dippy';
export { StorageRecord as ResourceType } from '@tinkoff/htmlpagebuilder';

const HEAD_META = 'head:meta';
const HEAD_PERFORMANCE = 'head:performance';
const HEAD_CORE_STYLES = 'head:core-styles';
const HEAD_POLYFILLS = 'head:polyfills';
const HEAD_CORE_SCRIPTS = 'head:core-scripts';
const HEAD_DYNAMIC_SCRIPTS = 'head:dynamic-scripts';
const HEAD_ANALYTICS = 'head:analytics';
const HEAD_ICONS = 'head:icons';
const BODY_START = 'body:start';
const REACT_RENDER = 'react:render';
const BODY_END = 'body:end';
const BODY_TAIL_ANALYTICS = 'body:tail:analytics';
const BODY_TAIL = 'body:tail';

var slots = {
  __proto__: null,
  HEAD_META: HEAD_META,
  HEAD_PERFORMANCE: HEAD_PERFORMANCE,
  HEAD_CORE_STYLES: HEAD_CORE_STYLES,
  HEAD_POLYFILLS: HEAD_POLYFILLS,
  HEAD_CORE_SCRIPTS: HEAD_CORE_SCRIPTS,
  HEAD_DYNAMIC_SCRIPTS: HEAD_DYNAMIC_SCRIPTS,
  HEAD_ANALYTICS: HEAD_ANALYTICS,
  HEAD_ICONS: HEAD_ICONS,
  BODY_START: BODY_START,
  REACT_RENDER: REACT_RENDER,
  BODY_END: BODY_END,
  BODY_TAIL_ANALYTICS: BODY_TAIL_ANALYTICS,
  BODY_TAIL: BODY_TAIL
};

/**
 * @description
 * Token adding resources to specific render slots.
 * Used only on server and it is equivalent to usage of RESOURCES_REGISTRY.
 *
 * [Usage example](https://tramvai.dev/docs/how-to/render-add-resources)
 */
const RENDER_SLOTS = createToken('RENDER_SLOTS', {
    multi: true,
});
/**
 * @description
 * Token for defining additional attributes for html, body and app container. Keep in mind that:
 *  - only raw html attributes are allowed (not react or anything else, e.g. do not use className use class)
 *  - new attributes overrides old one
 *  - only text values are supported
 *
 * @example
  ```tsx
  {
    provide: HTML_ATTRS,
    useValue: {
      target: 'body',
      attrs: {
        class: 'custom-class',
      },
    },
    multi: true,
  },
  ```
 */
const HTML_ATTRS = createToken('HTML_ATTRS', { multi: true });
/**
 * @description
 * Add node-style callback on render event in browser.
 * Passes error instance as first argument if there was an error
 */
const RENDERER_CALLBACK = createToken('RENDERER_CALLBACK', {
    multi: true,
});
/**
 * @description
 * Used as async function which overrides app render. This function may define render parameters or override render result.
 */
const CUSTOM_RENDER = createToken('CUSTOM_RENDER');
/**
 * @description
 * Resources registry is used only on server for registering any additional assets for browser (scripts, styles, html) which should be added to response html page
 */
const RESOURCES_REGISTRY = createToken('resourcesRegistry');
/**
 * @description
 * A string used for check of need to loading polyfills in the client browser.
 * Polyfills are always loading for browsers without module support and if browser do support modules
 * this check will called in order to find out the need of loading polyfills
 * (by default checks for Promise.prototype.finally and implementations for URL and URLSearchParams)
 *
 * [Polyfill documentation](https://tramvai.dev/docs/how-to/how-to-enable-polyfills)
 */
const POLYFILL_CONDITION = createToken('POLYFILL_CONDITION');
/**
 * @deprecated tramvai will automatically detect React version, and use hydrateRoot API for 18+ version
 * For Strict Mode, use token `USE_REACT_STRICT_MODE`
 */
const RENDER_MODE = createToken('RENDER_MODE');
/**
 * @description add Strict Mode wrapper, more info available in documentation https://reactjs.org/docs/strict-mode.html
 */
const USE_REACT_STRICT_MODE = createToken('useReactStrictMode');
/**
 * @description
 * A wrapper for app render.
 * Through that token is possible to specify for example React.Context.Provider for the app
 */
const EXTEND_RENDER = createToken('EXTEND_RENDER', {
    multi: true,
});
/**
 * @description
 * Token for default layout for all pages - root layout
 */
const DEFAULT_LAYOUT_COMPONENT = createToken('defaultLayoutComponent');
/**
 * @description
 * Token for default header for page
 */
const DEFAULT_HEADER_COMPONENT = createToken('defaultHeaderComponent');
/**
 * @description
 * Token for default footer for page
 */
const DEFAULT_FOOTER_COMPONENT = createToken('defaultFooterComponent');
/**
 * @description
 * Token for default Error Boundary for page
 */
const DEFAULT_ERROR_BOUNDARY_COMPONENT = createToken('defaultErrorBoundaryComponent');
/**
 * @description
 * Token for passing parameters for page layout
 */
const LAYOUT_OPTIONS = createToken('layoutOptions', {
    multi: true,
});
/**
 * @description
 * Settings for HTML resources inlining
 * * threshold Maximum value (in bytes) of the file which are getting inlined in HTML page
 * * types Types of resources which should be inlined
 */
const RESOURCE_INLINE_OPTIONS = createToken('resourceInlineThreshold');
/**
 * @description
 * Experimental switcher from ssr to client-side rendering mode
 */
const TRAMVAI_RENDER_MODE = createToken('tramvaiRenderMode');
const RENDER_FLOW_AFTER_TOKEN = createToken('render flow after', {
    multi: true,
});
const MODERN_SATISFIES_TOKEN = createToken('modernSatisfies');
const REACT_SERVER_RENDER_MODE = createToken('reactServerRenderMode');

export { CUSTOM_RENDER, DEFAULT_ERROR_BOUNDARY_COMPONENT, DEFAULT_FOOTER_COMPONENT, DEFAULT_HEADER_COMPONENT, DEFAULT_LAYOUT_COMPONENT, EXTEND_RENDER, HTML_ATTRS, LAYOUT_OPTIONS, MODERN_SATISFIES_TOKEN, POLYFILL_CONDITION, REACT_SERVER_RENDER_MODE, RENDERER_CALLBACK, RENDER_FLOW_AFTER_TOKEN, RENDER_MODE, RENDER_SLOTS, RESOURCES_REGISTRY, RESOURCE_INLINE_OPTIONS, slots as ResourceSlot, TRAMVAI_RENDER_MODE, USE_REACT_STRICT_MODE };
