import { createToken } from '@tinkoff/dippy';

/**
 * @description
 * Specifies base url for public papi handlers. By default equals to `/[appName]/papi`
 */
const SERVER_MODULE_PAPI_PUBLIC_URL = createToken('serverModulePapiPublicUrl');
/**
 * @description
 * Specifies base url for private papi handlers. By default equals `/[appName]/private/papi`
 */
const SERVER_MODULE_PAPI_PRIVATE_URL = createToken('serverModulePapiPrivateUrl');
/**
 * @description
 * Add private papi route
 */
const SERVER_MODULE_PAPI_PRIVATE_ROUTE = createToken('serverModulePapiPrivateRoute', {
    multi: true,
});
/**
 * @description
 * Add public papi route
 */
const SERVER_MODULE_PAPI_PUBLIC_ROUTE = createToken('serverModulePapiPublicRoute', {
    multi: true,
});
/**
 * @description
 * Settings for the static server
 */
const SERVER_MODULE_STATICS_OPTIONS = createToken('serverModuleStaticsOptions');
/**
 * @description
 * Instance of nodejs `http.Server`.
 * Can be used for adding custom logic on server, like error handling, connection settings
 *
 * @example
  ```tsx
  {
    provide: commandLineListTokens.init,
    multi: true,
    useFactory: ({ server }) => {
      return function serverListen() {
        createTerminus(server, {});
      };
    },
    deps: {
      SERVER_TOKEN,
    },
  },
  ```
 */
const SERVER_TOKEN = createToken('server');
/**
 * @description
 * Add resources for request proxying  to the app through `http-proxy-middleware`
 */
const PROXY_CONFIG_TOKEN = createToken('proxyConfigToken', {
    multi: true,
});
/**
 * @description
 * Override filter function when accessing papi route `/dependenciesVersion`
 */
const DEPENDENCIES_VERSION_FILTER_TOKEN = createToken('dependenciesVersionFilter');
/**
 * @description
 * List of the utility URLs on server (e.g. healthz and readyz)
 * Url matching is happens with a library `path-to-regexp`.
 */
const UTILITY_SERVER_PATHS = createToken('server utility paths', { multi: true });
/**
 * @description
 * Defines port to listen for utility routes
 */
const UTILITY_SERVER_PORT_TOKEN = createToken('server utility server port');
/**
 * @description
 * Custom path for liveness probe
 */
const LIVENESS_PATH_TOKEN = createToken('liveness path');
/**
 * @description
 * Custom path for readiness probe
 */
const READINESS_PATH_TOKEN = createToken('readiness path');
/**
 * @description
 * Custom function for k8s readiness, you might want to wait for something before allowing traffic to your app\
 * https://kubernetes.io/docs/tasks/configure-pod-container/configure-liveness-readiness-startup-probes/
 */
const READINESS_PROBE_TOKEN = createToken('readiness-probe-fn');
/**
 * @description
 * Custom function for k8s liveness, a function accepting a state and returning a promise indicating service health\
 * https://kubernetes.io/docs/tasks/configure-pod-container/configure-liveness-readiness-startup-probes/
 */
const LIVENESS_PROBE_TOKEN = createToken('liveness-probe-fn');

export { DEPENDENCIES_VERSION_FILTER_TOKEN, LIVENESS_PATH_TOKEN, LIVENESS_PROBE_TOKEN, PROXY_CONFIG_TOKEN, READINESS_PATH_TOKEN, READINESS_PROBE_TOKEN, SERVER_MODULE_PAPI_PRIVATE_ROUTE, SERVER_MODULE_PAPI_PRIVATE_URL, SERVER_MODULE_PAPI_PUBLIC_ROUTE, SERVER_MODULE_PAPI_PUBLIC_URL, SERVER_MODULE_STATICS_OPTIONS, SERVER_TOKEN, UTILITY_SERVER_PATHS, UTILITY_SERVER_PORT_TOKEN };
